let self;
export default {
	data() {
		return {
			isPositon: false, //是否已经位置授权
			modalName: null
		};
	},
	created() {
		self = this
		/* 获取是否已经拥有权限 */
		uni.getSetting({
			success({ authSetting }) {
				console.log(authSetting)
				if (!authSetting["scope.userLocationBackground"]) {
					console.log("没有前后台权限===>")
					self.isPositon = true
				}
			},
			fail() {
				console.log("获取失败")
			}
		})
	},
	methods: {
		openSetting() {
			/* 打开权限 */
			uni.openSetting({
				success({ authSetting }) {
					// console.log(authSetting.scope.userLocationBackground)
					if (!authSetting["scope.userLocationBackground"]) {
						uni.showToast({
							icon: "none",
							title: "请打开定位权限"
						})
					} else {
						self.isPositon = false
					}
				},
				fail(res) {
					console.log("失败", res)
				}
			});
		}
	}
}
